import React, { Component } from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import { getBrowserWidth } from 'utils/dom'

import s from './FastGrid.styl'

import computeTransforms from './compute-transforms'
import LIST from './grid.data'

class FastGridItem extends Component {
  static propTypes = {
    image: PropTypes.string.isRequired
  }
  constructor(props) {
    super(props)
    this.state = {
      loaded: false,
      loadedImage: false,
      image: props.image,
      hasError: false
    }
  }

  componentDidMount = () => {
    setTimeout(() => this.loadImage(), 300)
  }

  loadImage = () => {
    const img = document.createElement('img')
    img.onload = () => {
      this.setState({ loaded: true, loadedImage: this.state.image })
    }
    img.onerror = () => {
      this.setState({ hasError: true })
    }
    img.src = this.state.image
  }

  render() {
    const cx = classnames('fast-grid-item', {
      'is-loaded': this.state.loaded
    })

    const src = this.state.loadedImage || false

    return (
      <div className={cx} onTransitionEnd={this.props.onTransitionEnd}>
        {!!src && <img alt="" className="fast-grid-item-image" src={src} />}
      </div>
    )
  }
}

export default class FastGrid extends Component {
  state = {
    columnCount: 3
  }

  componentDidMount = () => {
    this.transitionCounter = 0
    this.handleResize()
    window.ss = this
    // this.handleScroll()
    window.addEventListener('scroll', this.handleScroll)
    window.addEventListener('resize', this.handleResize)
  }
  componentWillUnmount = () => {
    window.removeEventListener('scroll', this.handleScroll)
    window.removeEventListener('resize', this.handleResize)
  }

  handleResize = () => {
    if (this.isLayoutBusy) {
      return
    }
    this.isLayoutBusy = true
    window.requestAnimationFrame(this.updateColumns)
  }

  handleScroll = () => {
    if (this.isLayoutBusy) {
      return
    }
    this.isLayoutBusy = true
    window.requestAnimationFrame(this.updateLayout)
  }

  updateColumns = () => {
    const w = getBrowserWidth()
    var columnCount
    if (w < 650) {
      columnCount = 1
    } else if (w >= 650 && w <= 1160) {
      columnCount = 2
    } else {
      columnCount = 3
    }

    if (this.state.columnCount !== columnCount) {
      return this.setState({ columnCount }, () => {
        this.isLayoutBusy = false
      })
    }
    this.isLayoutBusy = false
  }

  updateLayout = () => {
    const items = Array.from(document.querySelectorAll('.fast-grid-item'))
    this.transitionCounter++
    items.forEach(computeTransforms)
  }

  onTransitionEnd = () => {
    this.transitionCounter--
    if (this.transitionCounter <= 0) {
      this.isLayoutBusy = false
      console.log('ended last', this.isLayoutBusy)
    }
    console.log('ended transition', this.transitionCounter, this.isLayoutBusy)
  }

  getColumns = () => {
    const count = this.state.columnCount
    const columns = LIST.concat(LIST).reduce((result, item, index) => {
      result[index % count].push(item)
      return result
    }, new Array(count).fill(1).map(x => []))
    return columns
  }

  render() {
    const cols = this.getColumns()
    return (
      <div className={classnames(s.container, 'fast-grid')}>
        {cols.map((col, i) => (
          <div className="grid-column" key={i}>
            {col.map((item, j) => (
              <FastGridItem
                key={j}
                image={item.src}
                onTransitionEnd={this.onTransitionEnd}
                onTransitionStart={this.onTransitionStart}
              />
            ))}
          </div>
        ))}
      </div>
    )
  }
}
