import { getBrowserHeight } from 'utils/dom'

// how much should visible area be offset from browser height
const OFFSET_FACTOR = 0.01

function getScaleFactor(bounds, y1, y2, h) {
  var elcenter = bounds.top + bounds.height / 2
  var pagecenter = h / 2
  var factor

  // left way above
  if (bounds.bottom <= -0.3 * h) {
    factor = 10
  } else if (bounds.top >= y1 && bounds.bottom <= y2) {
    factor = 0
  } else if (bounds.bottom > 0 && bounds.bottom < y1) {
    factor = (pagecenter - elcenter) / pagecenter
  } else if (bounds.top > y2 && bounds.top < h) {
    factor = (elcenter - pagecenter) / pagecenter
  } else {
    factor = 10
  }
  return factor
}

function buildTransform(item, factor) {
  var x = 0
  var y = factor > 0 ? 40 : -40
  var z = 0
  // var deg = factor < 0 ? 5 : -5
  return `translate3d(${x}px, ${y}px, ${z}px)`
}

export default function computeTransform(el) {
  var h = getBrowserHeight()
  var y1 = h * OFFSET_FACTOR
  var y2 = h * (1 - OFFSET_FACTOR)

  var bounds = el.getBoundingClientRect()
  var factor = getScaleFactor(bounds, y1, y2, h)

  // if (Math.abs(factor) === 10 && el.style.visibility !== 'hidden') {
  //   el.style.visibility = 'hidden'
  //   return
  // }

  // if (el.style.visibility !== 'visible') {
  //   el.style.visibility = 'visible'
  // }

  el.style.transform = buildTransform(el, factor)
}
